@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700;900&display=swap");
*,
*:before,
*:after {
  box-sizing: border-box;
}

:root {
  font-family: "Roboto", sans-serif;
  --Color-primario: #a4de31;
  --Color-primario-secundario: #08540a;
  --Color-letra: white;
}

ul {
  list-style: none;
}

body {
  min-width: 1820px;
  max-width: 100vw;
  background-color: #f1f1f1;
}
img {
  width: 100%;
}
a {
  text-decoration: none;
}
hr {
  margin: 0;
}

.header {
  min-width: 1820px;
  max-width: 100vw;
  height: 120px;
  background-color: var(--Color-primario);
  display: flex;
  align-content: center;
}

.header__content {
  width: 100vw;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__content__logo {
  height: 100px;
  width: 200px;
  margin-left: 200px;
}

.header__content__logo img {
  height: 100%;
}
.header__content__nav {
  display: flex;
  justify-content: flex-end;
  margin-right: 200px;
  width: 900px;
}

.header__content__nav ul {
  display: flex;
  align-items: center;
  gap: 40px;
  margin-bottom: 0;
}

.header__content__nav li {
  display: flex;
  font-size: 1.3em;
  color: var(--Color-letra);
  cursor: pointer;
}

.header__content__nav__button {
  background-color: var(--Color-primario-secundario);
  width: 110px;
  height: 38px;
  border: 0;
  border-radius: 10px;
  color: var(--Color-letra);
  cursor: pointer;
}

.option_user {
  background-color: var(--Color-primario-secundario);
  height: 80px;
  width: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 80px;
  border: 0;
}
.option_user h3 {
  color: white;
  font-size: 2em;
  font-weight: 500;
  -webkit-user-select: none;
}

/*PAGINA HOME------------------------------------------------------*/

/*PORTADA PERRO*/

.portada_home {
  background-image: url(../img/portada_perro.svg);
  background-size: 100%;
  background-repeat: no-repeat;
  color: white;
  min-width: 1750px;
  max-width: 100%;
  min-height: 930px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding-right: 100px;
}

.portada_home_texto {
  display: flex;
  justify-content: flex-end;
  text-align: center;
  font-size: 6em;
}

.portada_home_texto h3 {
  font-weight: 300;
  font-size: 1.1em;
  margin: 0;
}
.portada_home_texto span {
  font-weight: 700;
  font-size: 1.1em;
}

.portada_home_texto h3,
span {
  text-shadow: 3px 3px 10px black;
}

/*ADOPTAR HOME*/

.adoptar_home {
  min-width: 1750px;
  max-width: 100%;
  min-height: 1100px;
  background-color: #f1f1f1;
}

.home_card_adoptar {
  display: flex;
  flex-direction: column;
  margin-top: 20px;
}

.home_card_adoptar_title {
  text-align: center;
  font-size: 2em;
  color: var(--Color-primario-secundario);
  margin-bottom: 20px;
}
.home_card_adoptar_title h2 {
  font-size: 1.4em;
}

.home_card_adoptar_cards {
  display: flex;
  justify-content: center;
  gap: 50px;
}

.home_card_adoptar_cards_card {
  background-color: #000;
  width: 330px;
  height: 389px;
}

/*HOME RAZONES*/

.home_razones {
  margin-top: 60px;
  display: flex;
  justify-content: center;
  gap: 60px;
}

.home_razones_image {
  width: 700px;
  height: 470px;
}

.home_razones_text {
  color: var(--Color-primario-secundario);
  width: 850px;
  height: 470px;
  font-size: 2em;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.home_razones_text h2 {
  font-size: 1.4em;
}

.home_razones_text p {
  line-height: 50px;
  font-weight: 300;
}

/*PORTADA GATO*/

.portada_footer {
  background-image: url(https://i.ibb.co/tCDnsmy/Sin-t-tulo-5.jpg);
  background-size: 100%;
  background-repeat: no-repeat;
  color: white;
  min-width: 1750px;
  max-width: 100%;
  min-height: 880px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding-right: 100px;
}

.portada_footer_texto {
  display: flex;
  justify-content: flex-end;
  text-align: center;
  font-size: 6em;
}

.portada_footer_texto h3 {
  font-weight: 300;
  margin: 0;
  font-size: 1.1em;
}
.portada_footer_texto span {
  font-weight: 700;
  font-size: 1.1em;
}
.portada_footer_texto div span,
.portada_footer_texto div h3 {
  display: inline;
}

.portada_footer_texto h3,
span {
  text-shadow: 3px 3px 10px black;
}

/*FOOTER*/
.footer {
  min-width: 1820px;
  max-width: 100vw;
  height: 120px;
  background-color: var(--Color-primario);
  display: flex;
  align-content: center;
}

.footer__content {
  width: 100vw;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.footer__content__logo {
  height: 100px;
  width: 200px;
  margin-left: 200px;
}
.footer__content__logo img {
  height: 100%;
}

.footer__content__text {
  margin-right: 200px;
  color: var(--Color-letra);
  font-size: 1.4em;
}
.footer__content__text h3 {
  font-weight: 300;
}
/*PAGINA LOGIN-----------------------------------------------------*/

.login_main {
  display: flex;
  min-width: 1820px;
  max-width: 100%;
  min-height: 947px;
  max-height: 100%;
}

.login_form {
  width: 38%;
  height: 947px;
}
.login_image {
  width: 100%;
  height: 947px;
  background-image: url(https://i.ibb.co/KsbyDzh/login-portada.jpg);
  background-repeat: no-repeat;
  background-size: 100%;
  color: var(--Color-letra);
  text-shadow: 3px 3px 10px black;
}
.login_image_text {
  display: inline-block;
  margin-top: 190px;
  text-align: center;
  margin-left: 50px;
}

.login_image_text h2 {
  font-size: 4em;
  font-weight: 300;
  margin: 0;
}

.login_image_text h2 span {
  font-weight: 700;
  margin: 0;
}

.login_form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.login_form_image {
  width: 390px;
}

.login_form_content {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 10px;
}

.login_form_content input {
  margin-top: 20px;
  height: 60px;
  width: 400px;
  border: 2px solid black;
  border-radius: 10px;
  padding-left: 20px;
  font-weight: 300;
  font-size: 1.2em;
}

.login_form_content input:focus {
  border: none;
}

.login_form_content input:active {
  border: none;
}

.login_form_content h3 {
  color: black;
  margin: 20px 0;
  text-align: center;
  font-weight: 300;
  font-size: 1em;
}
.login_form_content h3:hover {
  font-weight: 700;
  cursor: pointer;
}

.login_form_content button {
  width: 200px;
  height: 50px;
  border: none;
  background-color: black;
  color: var(--Color-letra);
  font-weight: 500;
  font-size: 1.3em;
  border-radius: 5px;
}

.login_form_content button:hover {
  -webkit-transform: scale(0.5);
  -ms-transform: scale(0.5);
  transform: scale(1.1);
  transition: transform 0.5s;
}

/*PAGINA REGISTER-----------------------------------------------------*/
.register_main {
  display: flex;
  min-width: 1820px;
  max-width: 100%;
  min-height: 827px;
  max-height: 100%;
}

.register_main_form {
  width: 70%;
  min-height: 827px;
  max-height: 100%;
}

.register_main_image {
  width: 30%;
  background-image: url(https://i.ibb.co/k0Qdkwf/PASO.jpg);
  background-size: 100%;
  min-height: 827px;
  max-height: 100%;
}

.form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 50px;
}
.form button {
  width: 250px;
  height: 60px;
  margin-left: 150px;
  border: 0;
  background-color: var(--Color-primario-secundario);
  border-radius: 10px;
  font-size: 1.5em;
  font-weight: 500;
  color: var(--Color-letra);
}

.form_send h4 {
  margin-left: 130px;
  font-size: 1.2em;
  color: coral;
}

.form button:hover {
  -webkit-transform: scale(0.5);
  -ms-transform: scale(0.5);
  transform: scale(1.1);
  transition: transform 0.5s;
}
.register_main_form {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.register_main_form_title h1 {
  font-size: 5em;
  color: var(--Color-primario-secundario);
  font-weight: 500;
  margin-left: 150px;
}

.register_form {
  margin-top: 20px;
  display: flex;
  gap: 50px;
}
.register_form_campo {
  display: flex;
  gap: 20px;
  justify-content: flex-end;
  align-items: center;
  margin-bottom: 20px;
}

.register_form_campo h3 {
  display: flex;
  font-size: 1.5em;
  font-weight: 300;
  margin: 0;
  color: var(--Color-primario-secundario);
}
.register_form_campo input,
select {
  width: 300px;
  height: 50px;
  border: 2px solid var(--Color-primario-secundario);
  border-radius: 5px;
  font-size: 1.3em;
  padding: 2px 5px;
  color: var(--Color-primario-secundario);
}

/*MODAL NOTIFICATION*/
.notification {
  background-color: rgba(0, 0, 0, 0.5);
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  display: none;
  justify-content: center;
  align-items: center;
}

.notification_content {
  background-color: white;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  width: 500px;
  height: 210px;
}

.notification_title {
  margin-top: 20px;
  text-align: center;
}

.notification_title h3 {
  color: var(--Color-primario-secundario);
  font-weight: 700;
}

.notification_body {
  margin-top: 10px;
  padding: 0 20px;
  text-align: center;
}

.notification_body p {
  font-size: 1.3em;
}

.notification_button {
  margin-top: 5px;
  display: flex;
  justify-content: center;
}

.notification_button button {
  height: 40px;
  width: 100px;
  border: 0;
  border-radius: 5px;
  background-color: var(--Color-primario);
  color: var(--Color-letra);
  font-size: 1.3em;
  font-weight: 500;
}

/*MODAL OPCIONES USARIO*/
.modal_user {
  position: absolute;
  display: none;
  height: 110px;
  width: 200px;
  top: 130px;
  right: 150px;
  background-color: white;
  border-radius: 5px;
}

.modal_user_content {
  display: flex;
  flex-direction: column;
}

.model_user_content_name {
  width: 100%;
  height: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #eee;
}

.model_user_content_name h4 {
  margin: 0;
  font-size: 0.9em;
}

.model_user_content_options {
  width: 100%;
  height: 40px;
  display: flex;
  align-items: center;
}
.model_user_content_options a {
  text-decoration: none;
  color: black;
}
.model_user_content_options_option {
  display: flex;
  align-items: center;
  margin-left: 10px;
}

.model_user_content_options:hover {
  cursor: pointer;
  background-color: #eee;
}

.model_user_content_options h4 {
  margin: 0;
  margin-left: 5px;
  font-size: 1em;
}

.model_user_content_options img {
  width: 20px;
}

/*GRID PETS*/

.main_pets_title {
  padding: 50px 0;
  text-align: center;
}

.main_pets_title h2 {
  font-size: 4em;
  font-weight: 700;
  color: var(--Color-primario-secundario);
}

.grid_pets {
  display: grid;
  grid-row-gap: 100px;
  grid-template-columns: repeat(4, 400px);
  justify-content: center;
  margin-bottom: 150px;
}

/*CARD PETS*/
.card_pet {
  margin-top: 20px;
  width: 330px;
  height: 580px;
  box-shadow: 0px 2px 5px 1px rgba(0, 0, 0, 0.5);
}

.card_pet_content {
  display: flex;
  flex-direction: column;
  background-color: white;
}
.card_pet_content_image {
  width: 330px;
  height: 389px;
}
.card_pet_content_body {
  width: 100%;
  height: 190px;
}

.card_pet_content_body_title {
  margin-top: 10px;
  text-align: center;
}
.card_pet_content_body_title h3 {
  color: var(--Color-primario-secundario);
  font-size: 2em;
}

.card_pet_content_body_info h4 {
  margin: 0;
  font-size: 1.2em;
  font-weight: 300;
}

.card_pet_content_body_info h4 span {
  text-shadow: 0 0 0 #000;
  margin-right: 5px !important;
  margin: 0 20px;
  font-weight: 500;
}

